.header {
  // position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  background-color: rgba(7, 15, 43, 0.622);
  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    width: 100%;
    background-color: rgba(7, 15, 43, 0.622);
    height: 100%;
  }
  .wrapper {
    width: 1350px;
    margin: auto;
    .headermain {
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 9999;

      .logo {
        display: block;
        img {
          width: 150px;
          height: 100px;
        }
      }

      .headscroll {
        display: none;

        span {
          margin-top: 3px;
          width: 20px;
          display: block;
          height: 3px;
          background-color: #fff;
        }
      }

      .Headslidbar {
        background-color: #000;
        width: 0px;
        position: fixed;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 99;
        overflow: hidden;
        transition: 0.6s;

        .HeadBox {
          display: block;
          padding: 20px;
          input {
            padding: 10px;
            border-radius: 16px;
            border: none;
            outline: none;
          }
        }
        .headNavbar {
          ul {
            list-style: none;
            margin-top: 20px;
            li {
              margin-top: 30px;
              border-bottom: solid 1px #fff;
              padding: 20px;

              a {
                text-decoration: none;
                color: #fff;
              }
            }
          }
        }
        .HeadIconbar {
          display: block;
          padding: 20px;
          border-bottom: solid 1px #fff;
          a {
            color: #fff;
            font-size: 35px;
          }
        }
        .HeadIconbar1 {
          display: block;
          padding: 20px;
          border-bottom: solid 1px #fff;
          a {
            color: #fff;
            font-size: 16px;
            display: flex;
            gap: 20px;
          }
        }

        .Headnav {
          position: absolute;
          top: 7%;
          color: #fff;

          right: 40px;
          h2 {
            margin-top: -22px;
          }
        }
      }
      .headernav {
        display: block;

        ul {
          list-style: none;
          display: flex;
          gap: 40px;
          padding: 0;
          margin: 0;
          li {
            display: block;
            padding: 15px 0px;

            a {
              text-decoration: none;
              font-size: 20px;
              font-weight: 500;
              color: #fff;
              gap: 15px;
            }
          }
        }
      }
      .Icon {
        display: flex;
        gap: 10px;
        align-items: center;
        .whatsapp {
          text-decoration: none;
          border-right: solid 1px #ddd;
          padding: 0px 15px;
          i {
            font-size: 45px;
            color: #fff;
          }
        }
        .inquiry {
          text-decoration: none;
          display: flex;
          align-items: end;
          gap: 8px;
          border: solid 1px #fff;
          border-radius: 20px;
          padding: 10px 25px;
          color: #fff;
          font-size: 16px;
          font-weight: 500;
          &:hover {
            background-color: yellow;
            color: #000;
            font-weight: 600;
          }
          i {
            font-size: 20px;
            color: #fff;
          }
        }
        .form {
          background-color: hsla(0, 0%, 100%, 0.2);
          border-color: hsla(0, 0%, 100%, 0.09);
          padding: 10px 18px;
          border-radius: 20px;

          input {
            border: none;
            cursor: pointer;
            background: none;
            outline: none;
            &::placeholder {
              color: #fff;
              font-size: 16px;
            }
          }
          i {
            color: #fff;
          }
        }
      }
    }
  }
}

@media screen and (min-device-width: 992px) and (max-device-width: 1350px) {
  .wrapper {
    width: 100% !important;
    .headermain {
      padding-left: 20px;
      padding-right: 20px;
      .logo {
        img {
          width: 120px !important;
          height: 90px !important;
        }
      }

      .headernav {
        display: block;
        ul {
          list-style: none;
          display: flex;
          gap: 20px !important;
          padding: 0;
          margin: 0;
          li {
            display: block;
            a {
              text-decoration: none;
              font-size: 18px !important;
              font-weight: 500;
              color: #fff;
            }
          }
        }
      }
      .Icon {
        a {
          i {
            font-size: 35px !important;
          }
        }
        .inquiry {
          width: 120px;
          padding: 10px 13px !important;
          font-size: 13px !important;
          i {
            font-size: 20px !important;
          }
        }
        .form {
          width: 110px;
        }
      }
    }
  }
}

@media screen and (min-device-width: 768px) and (max-device-width: 991px) {
  .header {
    width: 100%;
    .wrapper {
      width: 100%;

      .logo {
        display: block;
        margin: auto;
      }
      .headernav {
        display: none !important;
      }
      .Icon {
        display: none !important;
      }
    }
    .headment {
      h1 {
        font-size: 37px !important;
      }
    }
  }
}

@media screen and (min-device-width: 360px) and (max-device-width: 767px) {
  .header {
    width: 100%;
    .wrapper {
      width: 100%;
      .headermain {
        position: relative;
        z-index: 11;
        .logo {
          display: block;
          margin: auto;
        }
        .headscroll {
          display: flex;
          margin-left: 20px;
          flex-direction: column;
          position: absolute;
          top: 60px;
          left: 15px;

          span {
            margin-top: 3px;
            width: 20px;
            display: block;
            height: 3px;
            background-color: #fff;
          }
        }

        .headernav {
          display: none !important;
        }
        .Icon {
          display: none !important;
        }
      }
      .headment {
        h1 {
          font-size: 27px !important;
        }
        h2 {
          font-size: 16px !important;
        }
      }
    }
  }
}
